$(document).ready(function() {

  //Burger
  $('.header__burger').click(function () {
      $(this).toggleClass('header__burger--active')
      $('.header__list').toggleClass('header__list--active')
      $('.header__desc').toggleClass('header__desc--active')
  })

    // Accordion
    $(".questions__item-show").click((function(){
        $(this).toggleClass("questions__item-show--active"),
        $(this).siblings(".questions__item-hidden").slideToggle()
    }));

    // Form
    $('.contacts__form-btn').click(function () {
        $(this).parents('.contacts__form').toggleClass('contacts__form--active')
        $('.contacts__thanks').toggleClass('contacts__thanks--active')
    })

})